@mixin poppins-700 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}

@mixin poppins-500 {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}

@mixin poppins-400 {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}

@mixin inter-700 {
  font-family: "Inter", sans-serif;
  font-weight: 700;
}

@mixin inter-500 {
  font-family: "Inter", sans-serif;
  font-weight: 500;
}
